import { prefix } from '../../globals/settings';
import { html, LitElement } from 'lit-element';
import { property } from 'lit/decorators.js';
import { carbonElement as customElement } from '../../globals/decorators/carbon-element';
import styles from './section-title.scss';
import Information16 from '@carbon/icons/lib/information/16';

import '../tooltip/tooltip';

/**
 * Section Title.
 *
 * @element cds-section-title
 */

@customElement(`${prefix}-section-title`)
export class CDSSectionTitle extends LitElement {
  static styles = styles;
  @property({ type: String }) title = 'Default Title';
  @property({ type: String }) tooltipText = 'Default tooltip text';
  @property({ type: String }) tooltipAlignment = 'bottom';
  @property({ type: Boolean }) showTooltip = true;

  render() {
    const { title, tooltipText, showTooltip, tooltipAlignment } = this;

    return html`
      <div>
        <div class="section-title">
          <div class="section-title__title">${title}</div>
          ${showTooltip
            ? html`<div class="section-title__tooltip">
                <cds-tooltip align=${tooltipAlignment}>
                  <div class="sb-tooltip-trigger" aria-labelledby="content">
                    ${Information16()}
                  </div>
                  <cds-tooltip-content id="content">
                    ${tooltipText}
                  </cds-tooltip-content>
                </cds-tooltip>
              </div> `
            : ''}
        </div>
        <div class="underline"></div>
      </div>
    `;
  }
}
